/*Less*/
/*advantages*/
.advantages__list {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 256px 256px 256px;
    grid-template-rows: 256px 331px 256px 256px;
  }
}
@media (min-width: 1200px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 400px 400px 400px;
  }
}
.advantages__item {
  padding: 24px 26px;
  background-color: #EEEEEE;
}
.advantages__item--blue-theme {
  background-color: #81B3D2;
  padding: 54px 19px 38px 19px;
}
.advantages__item--white-theme {
  padding: 44px 50px;
}
.advantages__item--icon {
  padding: 22px 19px;
  background-color: #ffffff;
  display: flex;
  align-items: flex-start;
}
.advantages__item--icon:nth-child(5) {
  margin-bottom: 24px;
}
@media (min-width: 768px) {
  .advantages__item--icon {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .advantages__item--icon:nth-child(5) {
    margin-bottom: 0px;
  }
}
.advantages__item__image {
  padding: 0;
}
@media (min-width: 768px) {
  .advantages__item__image--sity {
    width: 512px;
    height: 256px;
  }
}
@media (min-width: 1200px) {
  .advantages__item__image--sity {
    width: 800px;
    height: 256px;
  }
}
.advantages__item__image--bridge {
  width: 320px;
  height: 260px;
}
@media (min-width: 768px) {
  .advantages__item__image--bridge {
    width: 512px;
    height: 256px;
  }
}
@media (min-width: 1200px) {
  .advantages__item__image--bridge {
    width: 800px;
    height: 256px;
  }
}
@media (min-width: 768px) {
  .advantages__item:nth-child(2) {
    grid-column: 2/4;
    grid-row: 1/2;
  }
  .advantages__item:nth-child(7) {
    grid-column: 1/3;
    grid-row: 3/4;
  }
}
.advantages__list::before {
  background-color: #ffffff;
  content: "";
  width: 100%;
  height: 35px;
  position: absolute;
  top: -1px;
  clip-path: polygon(50% 50%, 0 0, 100% 0);
}
@media (min-width: 768px) {
  .advantages__list::before {
    clip-path: none;
    height: 0;
  }
}
.advantages__image-icon {
  margin-right: 13px;
}
@media (min-width: 768px) {
  .advantages__image-icon {
    margin: 0;
    margin-bottom: 33px;
  }
  .advantages__image-icon--house {
    width: 75px;
    height: 72px;
  }
  .advantages__image-icon--food {
    width: 76px;
    height: 68px;
  }
  .advantages__image-icon--gift {
    width: 64px;
    height: 77px;
  }
}
.advantages__title {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 700;
  color: #000000;
  text-align: center;
  line-height: 26px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 19px;
}
.advantages__title--icon {
  text-align: left;
}
@media (min-width: 768px) {
  .advantages__title--icon {
    text-align: center;
  }
}
.advantages__title--blue-theme {
  color: #ffffff;
}
.advantages__description {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  color: #000000;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
  margin: 0 auto;
}
.advantages__description--icon {
  text-align: left;
}
.advantages__description--blue-theme {
  color: #ffffff;
}
.advantages__description--num {
  margin-bottom: 22px;
}
@media (min-width: 768px) {
  .advantages__description {
    text-align: center;
  }
}
@media (min-width: 1200px) {
  .advantages__description {
    width: 250px;
  }
}
.advantages__picture {
  display: block;
}
