/*navigation*/
.navigation {
  position: relative;
  min-width: 56px;
  background-color: #ffffff;
  bottom: 96px;
}
@media (min-width: 768px) {
  .navigation {
    margin: 0;
    width: 100%;
    right: 0px;
  }
}
.navigation__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 3;
  display: block;
  width: 45px;
  height: 40px;
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .navigation__wrapper {
    width: 690px;
    margin: 0 auto;
    display: block;
  }
}
@media (min-width: 1200px) {
  .navigation__wrapper {
    width: 1040px;
    margin: 0 auto;
  }
}
.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin: 0 auto;
}
@media (max-width: 767px) {
  /*navigation--closed*/
  .navigation--closed .navigation__wrapper {
    display: none;
  }
  .navigation--closed .navigation__toggle::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 11px;
    width: 22px;
    height: 4px;
    background-color: #81B3D2;
    box-shadow: 0 8px 0 0 #81B3D2, 0 16px 0 0 #81B3D2;
  }
  .navigation--closed .navigation__toggle:active::before {
    background-color: rgba(129, 179, 210, 0.3);
    box-shadow: 0 8px 0 0 rgba(129, 179, 210, 0.3), 0 16px 0 0 rgba(129, 179, 210, 0.3);
  }
  /*navigation--opened*/
  .navigation--opened .navigation__wrapper {
    position: absolute;
    z-index: 2;
    width: 100%;
  }
  .navigation--opened .navigation__wrapper::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 7px;
    box-shadow: inset 4px 0 9px 0px rgba(0, 0, 0, 0.2);
  }
  .navigation--opened .navigation__toggle::before,
  .navigation--opened .navigation__toggle::after {
    z-index: 2;
    content: "";
    position: absolute;
    top: 20px;
    left: 13px;
    width: 23px;
    height: 4px;
    background-color: #ffffff;
  }
  .navigation--opened .navigation__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .navigation--opened .navigation__toggle::after {
    transform: rotate(-45deg);
  }
  .navigation--opened .navigation__toggle:active::before,
  .navigation--opened .navigation__toggle:active::after {
    background-color: rgba(255, 255, 255, 0.3);
  }
  /*navigation--nojs*/
  .navigation--nojs .navigation__wrapper {
    position: static;
    min-width: 0;
  }
  .navigation--nojs .navigation__toggle {
    display: none;
  }
}
